.book-search {
    max-width: 900px;
    margin: 2rem auto;
    padding: 2rem;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    background-color: #ffffff;
    border-radius: 12px;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  }
  
  h1 {
    color: #2c3e50;
    margin-bottom: 1.5rem;
  }
  
  form {
    display: flex;
    justify-content: center;
    gap: 1rem;
    flex-wrap: wrap;
    margin-bottom: 1.5rem;
  }
  
  input[type="text"] {
    padding: 0.75rem;
    width: 300px;
    border: 1px solid #ccc;
    border-radius: 8px;
    font-size: 1rem;
    transition: border-color 0.2s;
  }
  
  input[type="text"]:focus {
    border-color: #3498db;
    outline: none;
  }
  
  button {
    padding: 0.75rem 1.5rem;
    background-color: #3498db;
    color: white;
    border: none;
    border-radius: 8px;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.2s ease;
  }
  
  button:hover {
    background-color: #2980b9;
  }
  
  .upload {
    margin-top: 1rem;
    font-size: 0.9rem;
    color: #555;
  }
  
  .upload input[type="file"] {
    margin-top: 0.5rem;
  }
  
  .book-list {
    display: flex;
    flex-wrap: wrap;
    gap: 1rem;
    justify-content: center;
    margin-top: 2rem;
  }
  
  .book-item {
    background-color: #f9f9f9;
    padding: 1rem;
    width: 200px;
    border-radius: 8px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
    text-align: center;
  }
  
  .book-item img {
    width: 100%;
    height: auto;
    border-radius: 4px;
    margin-bottom: 0.5rem;
  }
  
  .book-item h3 {
    font-size: 1rem;
    color: #2c3e50;
    margin: 0.5rem 0;
  }
  
  .book-item p {
    font-size: 0.9rem;
    color: #7f8c8d;
  }
  
  .pagination {
    margin-top: 2rem;
    display: flex;
    justify-content: center;
    gap: 1rem;
  }
  
  .pagination button {
    padding: 0.6rem 1.2rem;
    background-color: #ecf0f1;
    color: #2c3e50;
    border: none;
    border-radius: 6px;
    font-weight: bold;
    cursor: pointer;
    transition: background-color 0.2s ease;
  }
  
  .pagination button:hover {
    background-color: #bdc3c7;
  }
  
  .pagination button:disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }
  